:root{
    --color-prim: #0909a9 ;
    --color-sec: #289fd0 ;
    --color-third: #4c4cf4 ;
    --color-accent: #eeca0d ;
    --color-sec-accent:#f8e26d;
    --color-heading: #7c7c7c ;
    --color-border: ghostwhite;
    --color-body: #7fc7eb ;
    --color-dark:#030343;

    
}
/*TYPOGRAPHY*/
::selection{
    background: var(--color-sec);
    color: var(--color-accent);
}

html{
    font-size: 62.5%;
}

img{
    width: 100%;
}

body{
  font-family: "Lora", serif;
  font-size: 2rem;
  line-height: 1.5;
  color: var(--color-body);
}

h1,
h2,
h3{
    color: var(--color-border);
    margin-bottom: 1rem;
    line-height: 1.1 ;
}
h1{
    font-size: 6rem;
}

h2{
    font-size: 4rem;
}
h3{
    font-size: 2.8rem;
    font-weight: 600;
    line-height: 1.3;
}

p{
    margin-top: 0;
}

/*text paletter */
.head-rubik{
        font-family: "Rubik Bubbles", system-ui;
        font-weight: 400;
        font-style: normal;
        color: var(--color-sec);
  }
  .sec-color{
    color: var(--color-sec);
  }

  .prim-color{
    color: var(--color-prim);
  }

  .accent-color{
    color: var(--color-accent);
  }
  
  .third-color{
    color: var(--color-third);
  }

.sec-accent-c{
    color: var(--color-sec-accent);
}


@media screen and (min-width: 1024px){

    body{
        font-size: 1.8rem;
    }

    h1{
        font-size: 8rem;
    }

    h2{
        font-size: 4rem;
    }

    h3{
        font-size: 2.4rem ;
    }
}















/*nav-bar*/
.nav-bar{
    background:var(--color-prim) ;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    padding: 1rem;
    align-items: center;
}

.var__list{
    width: 100%;
    margin: 0;
}

.list{
    list-style: none;
    padding-left: 0;
}

.nav__item{
    padding: 0.5rem 2rem ;
    border-bottom: 3px solid var(--color-sec) ;
}

.nav__item:last-of-type{
    border-bottom: none;
}

.nav__item > a {
    color: var(--color-heading);
}



/*collapsible*/

.collapsible__content{
    max-height: 0;
    overflow: hidden;
    opacity: 0;
    transition: all 0.3s;
}

/*A*/
a:hover{
    color: var(--color-accent);
    transform: color 0.3s;
}

a{
    
    text-decoration: none;
}
